.details-container {
  background: linear-gradient(rgba(32, 32, 32, 0.4), rgba(0,0,0,.40)100%), url('https://luise-rios.github.io/movie-library/theater.jpg');
  background-size: 100% 100%;
  color: var(--light);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  height: 100%;
}

.details-container .card {
  background: var(--light);
  color: var(--font-color);
  margin-top: 3rem;
  width: 50%;
}

.details-container .btn-group {
  margin: 1rem;
}
